function Reminder(){
    this.hours = 0;
    this.minutes = 0;
    this.text = "";
    this.enabled = true;
    this.lastReminded = new Date().getTime()    
}
function setDefaultReminders(){
    var reminder1 = new Reminder();
    reminder1.enabled = false;
    reminder1.hours = 0;
    reminder1.minutes = 20;
    reminder1.text = "Look away from your screen for 10 seconds and blink multiple times";
    var reminder2 = new Reminder();
    reminder2.enabled = false;
    reminder2.hours = 0;
    reminder2.minutes = 45;
    reminder2.text = "Don't forget to drink";
    var reminder3 = new Reminder();
    reminder3.enabled = false;
    reminder3.hours = 2;
    reminder3.minutes = 0;
    reminder3.text = "Take a rest from the screen, stand up and walk for a few minutes";    
    return {
        "1": reminder1,
        "2": reminder2,
        "3": reminder3,
    };
    /////////////////////////////////////////////////////////////
    return {
        "1": {
            hours: 0,
            minutes: 20,
            text: "Look away from your computer for 10 seconds and blink multiple times",
            lastReminded: new Date().getTime()
        },
        "2": {
            hours: 0,
            minutes: 45,
            text: "Don't forget to drink",
            lastReminded: new Date().getTime()
        },
        "3": {
            hours: 2,
            minutes: 0,
            text: "Take a rest from the screen, stand up and walk for a few minutes",
            lastReminded: new Date().getTime()
        }    
    };
}

