function revivePet(){
    chrome.tabs.query({}, function(tabs) {
        for (var i=0; i<tabs.length; ++i) {
            chrome.tabs.sendMessage(tabs[i].id, {"method": "revive", "data": appData.pets["cat1"]});
        }
    });
}

function ajaxRequest(data) {
    var xmlhttp = new XMLHttpRequest();
    var encodedString = '';
    for (var k in data.data) {
        if (encodedString.length > 0) {
            encodedString += '&';
        }
        encodedString += encodeURI(k + '=' + data.data[k]);
    }
    
    xmlhttp.onreadystatechange = function() {
        if (xmlhttp.readyState == XMLHttpRequest.DONE) {   // XMLHttpRequest.DONE == 4
           if (xmlhttp.status == 200) {
               if(data.success){
                   data.success.call(this, xmlhttp.responseText);
               }
               
           }
           else {
               if(data.error)
                    data.error.call(xmlhttp.responseText);
           }
        }
    };

    xmlhttp.open(data.type, data.url, true);
    xmlhttp.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
    xmlhttp.send(encodedString);
}


function isDevMode() {
    return !('update_url' in chrome.runtime.getManifest());
}

chrome.runtime.onMessage.addListener(
  function(request, sender, sendResponse) {
    // DATA TO SEND AS RESPONSE
    var data = {
        pet: appData.pets["cat1"]
    };
    
    if (request.method == "initiate"){            
        data.isDebug = isDevMode();
        chrome.tabs.query({}, function(tabs) {
            for (var i=0; i<tabs.length; ++i) {
                chrome.tabs.sendMessage(tabs[i].id, {"method": "browser-assets-ready", "data": {"pet":appData.pets["cat1"]}});
            }
        });
    }
    else if (request.method == "request-login"){
        var token = "";
        var login = localStorage.getItem("login");
        if(login){
            login = JSON.parse(login);
            token = login.login_token
        }
        data.token = token;
    }    
    else if (request.method == "PING"){            
        
    }    
    else if (request.method == "updatePositions"){
        if(!request.data.posLeft)
            request.data.posLeft = 0;
        if(!request.data.posTop)
            request.data.posTop = 0;
            
        appData.pets["cat1"].posLeft = request.data.posLeft;
        appData.pets["cat1"].posTop = request.data.posTop;
        localStorage.setItem("pets", JSON.stringify(appData.pets));
    } else if (request.method == "hide-30-min"){
            var returnAt = new Date().getTime() + (60000 * 30);
            appData.pets["cat1"].hideUntil = returnAt;
            localStorage.setItem("pets", JSON.stringify(appData.pets));
            
            chrome.tabs.query({}, function(tabs) {
                for (var i=0; i<tabs.length; ++i) {
                    chrome.tabs.sendMessage(tabs[i].id, {"method": "hide", "data": appData.pets["cat1"]});
                }
            });  

        
    } else if (request.method == "show-toggle"){
        appData.pets["cat1"].hideUntil = 0;

        if(request.data.status){
            appData.pets["cat1"].show = true;
            localStorage.setItem("pets", JSON.stringify(appData.pets));
            chrome.tabs.query({}, function(tabs) {
                for (var i=0; i<tabs.length; ++i) {
                    chrome.tabs.sendMessage(tabs[i].id, {"method": "revive", "data": appData.pets["cat1"]});
                }
            });  
        }
        else{
            appData.pets["cat1"].show = false;
            localStorage.setItem("pets", JSON.stringify(appData.pets));
            chrome.tabs.query({}, function(tabs) {
                for (var i=0; i<tabs.length; ++i) {
                    chrome.tabs.sendMessage(tabs[i].id, {"method": "hide", "data": appData.pets["cat1"]});
                }
            });
        }
    }
    else if (request.method == "walk-toggle"){
        appData.pets["cat1"].walk = request.data.status;
        localStorage.setItem("pets", JSON.stringify(appData.pets));
        
        chrome.tabs.query({}, function(tabs) {
            for (var i=0; i<tabs.length; ++i) {
                chrome.tabs.sendMessage(tabs[i].id, {"method": "update-options", "data": appData.pets["cat1"]});
            }
        });
    }
    else if (request.method == "change-chrome-notify"){
        appData.chromePopupReminders = request.data.status;
        localStorage.setItem("chromePopupReminders", (appData.chromePopupReminders));
    }    
    else if (request.method == "walk-type"){
        appData.pets["cat1"].walkType = request.data.status;
        localStorage.setItem("pets", JSON.stringify(appData.pets));
        
        chrome.tabs.query({}, function(tabs) {
            for (var i=0; i<tabs.length; ++i) {
                chrome.tabs.sendMessage(tabs[i].id, {"method": "update-options", "data": appData.pets["cat1"]});
            }
        });
    }    
    else if (request.method == "talk-toggle"){
        appData.pets["cat1"].talk = request.data.status;
        localStorage.setItem("pets", JSON.stringify(appData.pets));
        
        chrome.tabs.query({}, function(tabs) {
            for (var i=0; i<tabs.length; ++i) {
                chrome.tabs.sendMessage(tabs[i].id, {"method": "update-options", "data": appData.pets["cat1"]});
            }
        });
    }
    else if (request.method == "eat-request"){
        appData.pets["cat1"].hunger = appData.pets["cat1"].hunger + 20;
        appData.pets["cat1"].happiness = appData.pets["cat1"].happiness + 3;
        checkBarsLimits();
        
        doAction({"method": "action", "data": {
            name : "talk",
            parameters: {text: randomTalk("happy")}
        }});        
        
        localStorage.setItem("pets", JSON.stringify(appData.pets));  
    }
    else if (request.method == "petting"){
        appData.pets["cat1"].happiness = appData.pets["cat1"].happiness + 10;
        checkBarsLimits();
        localStorage.setItem("pets", JSON.stringify(appData.pets));
       
        var rand = Math.floor(Math.random() * (100)) + 1;
        if(rand < 20){
            doAction({"method": "action", "data": {
                name : "talk",
                parameters: {text: randomTalk("happy")}
            }});
        }
    }
    else if (request.method == "dancing"){
        appData.pets["cat1"].happiness = appData.pets["cat1"].happiness + 4;
        checkBarsLimits();
        localStorage.setItem("pets", JSON.stringify(appData.pets));
    }
        
    else if (request.method == "put-to-sleep"){
        appData.pets["cat1"].idleType = "sleep";
        
        doAction({"method": "action", "data": {
            name : "sleep",
            parameters: null
        }});        
    }     
    else if (request.method == "sleep-request"){
        appData.pets["cat1"].sleep = appData.pets["cat1"].sleep + 5;
        appData.pets["cat1"].happiness = appData.pets["cat1"].happiness + 3;
        checkBarsLimits();
        localStorage.setItem("pets", JSON.stringify(appData.pets));
    }    
    else if (request.method == "change-pet-name"){
        appData.pets["cat1"].name = request.data.name;
        localStorage.setItem("pets", JSON.stringify(appData.pets));
    }
    else if (request.method == "change-owner-name"){
        appData.ownerName = request.data.name;
        localStorage.setItem("ownerName", appData.ownerName);
    }
    else if (request.method == "reminder-add"){
        var reminder = new Reminder();
        reminder.text = request.data.text;
        reminder.enabled = request.data.enabled;
        reminder.hours = request.data.hours;
        reminder.minutes = request.data.minutes;
        appData.reminders[request.data.id] = reminder;
        localStorage.setItem("reminders", JSON.stringify(appData.reminders));
    }
    else if (request.method == "reminder-update"){        
        var reminder = appData.reminders[request.data.id];
        reminder.text = request.data.text;
        reminder.enabled = request.data.enabled;
        reminder.hours = request.data.hours;
        reminder.minutes = request.data.minutes;
        localStorage.setItem("reminders", JSON.stringify(appData.reminders));
    }    
    else if (request.method == "reminder-remove"){
        delete appData.reminders[request.data.id];
        localStorage.setItem("reminders", JSON.stringify(appData.reminders));
    } else if (request.method == "choose-browser-skin"){
        appData.pets["cat1"].skin = request.data.skin;
        localStorage.setItem("pets", JSON.stringify(appData.pets));
        revivePet();
        
    } else if (request.method == "choose-browser-hat"){
        appData.pets["cat1"].hat = request.data.hat;
        localStorage.setItem("pets", JSON.stringify(appData.pets));
        revivePet();
    } else if (request.method == "choose-browser-eyes"){
        appData.pets["cat1"].eyes = request.data.eyes;
        localStorage.setItem("pets", JSON.stringify(appData.pets));
        revivePet();
    }    
    sendResponse({data:data});
  });

  
