var App = {};
App.lastEvent = 0;
App.isUnhappy = function(val){
    if(!val)
        val = 20;
    return appData.pets["cat1"].happiness < val ? true : false;
};
App.isHungry = function(val){
    if(!val)
        val = 20;
    return appData.pets["cat1"].hunger < val ? true : false;
};
App.isSleepy = function(val){
    if(!val)
        val = 20;
    return appData.pets["cat1"].sleep < val ? true : false;
};

App.sendIdleMode = function(){
    chrome.tabs.query({}, function(tabs) {
        for (var i=0; i<tabs.length; ++i) {
            chrome.tabs.sendMessage(tabs[i].id, {"method": "update-idle", "data": appData.pets["cat1"]});
            //chrome.tabs.sendMessage(tabs[i].id, {"method": "action", "data": {name: "idle", pet:appData.pets["cat1"]}});
        }
    });    
}
var randomTalk = function(status){
    var item = "";
    switch(status){
        case "hungry":
            item = hungrySentences[Math.floor(Math.random()*hungrySentences.length)];
            break;
        case "unhappy":
            item = sadSentences[Math.floor(Math.random()*sadSentences.length)];
            break;
        case "happy":
            item = happySentences[Math.floor(Math.random()*happySentences.length)];
            break;
        case "dreaming":
            item = dreamingSentences[Math.floor(Math.random()*dreamingSentences.length)];
            break;            
        default:
            item = sentences[Math.floor(Math.random()*sentences.length)];
            break;
    }

    var petName = appData.pets["cat1"].name;
    var ownerName = appData.ownerName;
    
    if(!ownerName){
        ownerName = "Human";
    }
    if(!petName)
        petName = "Meow";
        
    item = item.replace(/{owner}/g, ownerName).replace(/{pet}/g, petName);
    return item;
};


var checkBarsLimits = function(){
    if(appData.pets["cat1"].idleType == "sleep" && appData.pets["cat1"].sleep >= 90){
        appData.pets["cat1"].idleType = "idle";
        App.sendIdleMode();
    }
    
    
    else if(appData.pets["cat1"].idleType == "sad" 
        && appData.pets["cat1"].happiness >= 40
        && appData.pets["cat1"].hunger >= 40
    ){
            appData.pets["cat1"].idleType = "idle";
            App.sendIdleMode();
    }
    
    
    if(appData.pets["cat1"].happiness > 100){
        appData.pets["cat1"].happiness = 100;
    }else if(appData.pets["cat1"].happiness < 0){
        appData.pets["cat1"].happiness = 0;
    }
    
    if(appData.pets["cat1"].hunger > 100){
        appData.pets["cat1"].hunger = 100;
    }else if(appData.pets["cat1"].hunger < 0){
        appData.pets["cat1"].hunger = 0;
    }    
    
    if(appData.pets["cat1"].sleep > 100){
        appData.pets["cat1"].sleep = 100;
    } else if(appData.pets["cat1"].sleep < 0){
        appData.pets["cat1"].sleep = 0;
    }
};


