var doAction = function(data){
    chrome.tabs.query({}, function(tabs) {
        for (var i=0; i<tabs.length; ++i) {
            chrome.tabs.sendMessage(tabs[i].id, data);
        }
    });
};

var randomEvent = function(){
    if(!appData.pets["cat1"].show)
        return;
    var curTime = (new Date()).getTime();
    // WE HAD EVENT LAST 4 SECONDS, NO NEED TO DO SOMETHING AGAIN
    if(curTime - App.lastEvent < 4000)
        return
    
    
    var rand = Math.floor(Math.random() * (450)) + 1;
    var action = null;
    var parameters = {
        text: ""
    };
    
    
    //IF SLEEPING, DON'T DO ANYTHING ELSE BESIDES CHANCE TO SLEEP
    if(appData.pets["cat1"].idleType == "sleep"){
        if(rand <= 20){
            action = "talk";
            parameters.text = randomTalk("dreaming");
        }  
    }
    // IF SLEEPY, PUT TO SLEEP
    else if(App.isSleepy()){
        appData.pets["cat1"].idleType = "sleep";
        action = "sleep";
    // IF NOT SAD YET - BUT SADNESS BELOW 40, MAKE IT SAD
    } else if(appData.pets["cat1"].idleType != "sad" && App.isUnhappy(40)){
        appData.pets["cat1"].idleType = "sad";
        action = "sad";
    // IF NOT SAD YET, BUT HUNGER BELOW 40 - MAKE IT SAD
    } else if(appData.pets["cat1"].idleType != "sad" && App.isHungry(40)){
        appData.pets["cat1"].idleType = "sad";
        action = "sad";
    }
    // IF HUNGER BELOW 20 - ONLY SAY HUNGER TALKS
    else if(App.isHungry()){
        if(rand <= 20){
            action = "talk";
            parameters.text = randomTalk("hungry");
        }
    }    
    // IF HUNGER BELOW 20 - ONLY SAY SADNESS TALKS
    else if(App.isUnhappy()){
        if(rand <= 20){
            action = "talk";
            parameters.text = randomTalk("unhappy");
        }
    }
    else if(rand >= 21 && rand <= 30 && appData.pets["cat1"].idleType != "sleep"){
            action = "run";
    }    
    else if(rand >= 31 && rand <= 40 && appData.pets["cat1"].idleType != "sleep"){
            action = "walk";
    } else if(rand >= 41 && rand <= 50 && appData.pets["cat1"].idleType != "sleep") {
            action = "talk";
            parameters.text = randomTalk("default");
    } else if(rand >= 51 && rand <= 60 && appData.pets["cat1"].idleType != "sleep") {
            action = "lick";
    }
    
    //console.log("Random Event:", action)
    if(action){
        App.lastEvent = curTime;
        doAction({"method": "action", "data": {
            name : action,
            parameters: parameters
        }});
    }
};




// EVERY 10 MIN NOW (UP FROM 4) 5 HOURS = 30
var barTicks = function(){    
    if(appData.pets["cat1"].idleType != "sleep"){
        appData.pets["cat1"].sleep-=3;
        appData.pets["cat1"].hunger-=3;
        appData.pets["cat1"].happiness-=2;        
    }
          
    checkBarsLimits();
    localStorage.setItem("pets", JSON.stringify(appData.pets));    
};


var minuteTicks = function(){
    if(appData.pets["cat1"].idleType == "sleep"){
        appData.pets["cat1"].sleep+=5;
    }
    checkBarsLimits();
    localStorage.setItem("pets", JSON.stringify(appData.pets));    
};

var checkReminders = function(){
    if(appData.reminders){
        for (var k in appData.reminders) {
            if (appData.reminders.hasOwnProperty(k)) {
                var now = new Date().getTime();
                var lastReminded = parseInt(appData.reminders[k].lastReminded);
                var secInterval = (appData.reminders[k].hours * 60 * 60) + (appData.reminders[k].minutes * 60);
                if(appData.reminders[k].enabled && appData.reminders[k].text && ((lastReminded / 1000) + secInterval) < (now / 1000)){
                
                    if (chrome.notifications && appData.chromePopupReminders){
                        chrome.notifications.create("Meow reminder", {
                            type: "basic",
                            title: "Reminder",
                            iconUrl: "/16.png",
                            message: appData.reminders[k].text
                            
                        }, function(evt){  
                        }); 
                    }
        
        
                    appData.reminders[k].lastReminded = now;
                    localStorage.setItem("reminders", JSON.stringify(appData.reminders));
                    doAction({"method": "action", "data": {
                        name : "talk",
                        parameters: {text: "<span class='reminder'>Reminder: " + appData.reminders[k].text + "</span>" }
                    }});
                    App.lastEvent = now;
                    return;
                }
                
            }
        }        
    }
};

// REDUCE BARS
window.setInterval(barTicks, 10 * 60 * 1000);
// IMPROVE SLEEP 
window.setInterval(minuteTicks, 1 * 60 * 1000);
// RANDOM MEOW BEHAVIOR
window.setInterval(randomEvent, 2 * 1000);
// REMINDERS SYSTEM
window.setInterval(checkReminders, 10 * 1000);









function installEvents(){
        // On install, send event
    if(chrome.runtime.onInstalled){
        try{
            chrome.runtime.onInstalled.addListener(function(details){
                if(details.reason == "install"){
                    window.open("https://www.meowplayground.com/welcome");
                    ajaxRequest({
                        url: "https://www.meowplayground.com/install",
                        type: "POST",
                        data: {
                            version: chrome.runtime.getManifest().version
                        }
                    });
                } else if(details.reason == "update"){
                    //var thisVersion = chrome.runtime.getManifest().version;
                    //console.log("Updated from " + details.previousVersion + " to " + thisVersion + "!");
                }
            });
        } catch(e){
            
        }
    }

    if(chrome.runtime.setUninstallURL) {
      chrome.runtime.setUninstallURL('https://www.meowplayground.com/uninstall');
    }

}

installEvents();